import { Component } from '@angular/core';
import { AngularFireStorage } from '@angular/fire/compat/storage';
// import { NavController } from '@ionic/angular/providers/nav-controller';
import { LoginService } from '../auth/login/login.service';
import { Carroservice } from '../oculos/service/oculo-service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  listaCarros;
  
  constructor(
    private service: LoginService,
    private oculo: Carroservice,
    private storage: AngularFireStorage
  ) {}
    
    
  ngOnInit() {
    this.oculo.lista().subscribe((x) => (this.listaCarros = x));
  }
  
  logOut() {
    this.service.logOut()
  }

  remove(oculo) {
    console.log(oculo)
    if(oculo.imageUrl) {
      this.storage.refFromURL(oculo.imageUrl).delete()
    }
    console.log(oculo)
    console.log(typeof oculo.id)
    this.oculo.remove(oculo);
  }
}
